*, *::after, *::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --bkg: #232946;
  --dark: #121629;
  --purple: #b8c1ec;
  --pink: #eebbc3;
  --white:#fffffe;
}

body {
  background-color: var(--bkg);
  display: grid;
  place-items: center;
  min-height: 100vh;
  font-family: ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 2rem;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 4rem;
  margin: 1rem;
}

.clock--wrapper {
  display: grid;
  place-items: center;
  gap: 2rem;
  color: var(--white);
}

.face {
  filter: drop-shadow(2px 10px 10px var(--dark));
}

.ring--minutes {
  fill: var(--dark);
  stroke: var(--pink);
  stroke-dasharray: .1 .9;
  stroke-dashoffset: .05;
  stroke-width: 3;
}

.ring--hours {
  fill: transparent;
  stroke: var(--purple);
  stroke-dasharray: .5 11.5;
  stroke-dashoffset: .25;
  stroke-width: 10;
}

.ring--center {
  stroke-width: 2.5;
  stroke: var(--white);
  fill: var(--pink);
}

.hand {
  stroke: var(--pink);
  stroke-linecap: round;
}

.hand--hour {
  stroke: var(--white);
  stroke-width: 5;
}

.hand--minute {
  stroke: var(--purple);
  stroke-width: 5;
}

.date {
  fill: var(--white);
  font-size: 1.5rem;
}

.am-pm {
  fill: var(--pink);
  font-size: .8rem;
  text-transform: uppercase;
}